












import Vue from "vue";

declare global {
  interface Window {
    gapi: any;
    google: any;
  }
}
type google = any;

export default Vue.extend({
  data(): {
    googFilePickerAPIKey: string;
    userOAuthToken: string;
    isPickerApiLoaded: boolean;
    error: string;
  } {
    return {
      googFilePickerAPIKey: [
        "AIzaSyBtJ",
        "IilOStGntZyOsEx9DEg",
        "DlkCbMSeTT0"
      ].join(""),
      userOAuthToken: "",
      isPickerApiLoaded: false,
      error: ""
    };
  },
  mounted(): void {
    if (!window.gapi) {
      this.error = "Unable to initialize Google File Picker";
      return window._logger(new Error(this.error));
    }

    if (window.google && window.google.picker) {
      this.isPickerApiLoaded = true;
    } else {
      const _this = this;
      window.gapi.load("picker", {
        callback: function() {
          _this.isPickerApiLoaded = true;
          _this.initPicker();
        }
      });
    }

    window._backend.withSuccessHandler(this.initPicker).uiGetOAuthToken();
  },

  methods: {
    initPicker(token?: string): void {
      if (token) {
        this.userOAuthToken = token;
      }

      // Avoid race condition between loading Google File Picker
      // API script and getting OAuh token. Whichever is called
      // last renders the picker.
      if (this.userOAuthToken && this.isPickerApiLoaded) {
        this.renderPicker();
      }
    },
    renderPicker(): void {
      const DIALOG_DIMENSIONS = {
        width: window.innerWidth,
        height: window.innerHeight
      };

      const pickerAPI = window.google.picker;
      const picker = new pickerAPI.PickerBuilder()
        // Instruct Picker to display only spreadsheets in Drive. For other
        // views, see https://developers.google.com/picker/docs/#otherviews
        .addView(pickerAPI.ViewId.SPREADSHEETS)
        // Hide the navigation panel so that Picker fills more of the dialog.
        .enableFeature(pickerAPI.Feature.NAV_HIDDEN)
        // Hide the title bar since an Apps Script dialog already has a title.
        .hideTitleBar()
        .setOAuthToken(this.userOAuthToken)
        .setDeveloperKey(this.googFilePickerAPIKey)
        .setCallback(this.pickerCallback)
        .setOrigin(window._frontend.origin)
        // Instruct picker to fill the dialog, minus 2 pixels for the border.
        .setSize(DIALOG_DIMENSIONS.width - 2, DIALOG_DIMENSIONS.height - 2)
        .build();
      picker.setVisible(true);
    },

    pickerCallback(data: any): void {
      const pickerAPI = window.google.picker;
      const action = data[pickerAPI.Response.ACTION];
      if (action == pickerAPI.Action.PICKED) {
        const doc = data[pickerAPI.Response.DOCUMENTS][0];
        const id = doc[pickerAPI.Document.ID];
        const url = doc[pickerAPI.Document.URL];
        const title = doc[pickerAPI.Document.NAME];

        this.$store.dispatch("clearSheet");
        this.$store.dispatch("setSpreadsheet", { id, url, title });

        window.gtag("event", "picker", {
          event_category: "engagement",
          event_label: title
        });

        window.saasmetrics("event", {
          category: "engagement",
          action: "picker",
          label: title
        });

        this.cancelFilePicker();
      } else if (action == pickerAPI.Action.CANCEL) {
        this.cancelFilePicker();
      }
    },

    cancelFilePicker(): void {
      this.$store.dispatch("setLocation", "/");
    }
  }
});
