




































import Vue from "vue";
import { mapState } from "vuex";
import LabelsCreateSpreadsheet from "./LabelsCreateSpreadsheet.vue";
import LabelsCreateProduct from "./LabelsCreateProduct.vue";
import LabelsCreateDesign from "./LabelsCreateDesign.vue";

declare global {
  interface Window {
    gtag: any;
    _backend: any;
    _frontend: any;
    _logger: any;
  }
}

export default Vue.extend({
  name: "LabelsCreate",
  components: {
    LabelsCreateSpreadsheet,
    LabelsCreateProduct,
    LabelsCreateDesign
  },
  data() {
    return {
      isMergeInProgress: false
    };
  },
  computed: {
    ...mapState(["spreadsheet", "sheet", "template", "product"]),
    isMergeDoable(): boolean {
      return (
        this.spreadsheet.id != "" &&
        this.sheet.title !== "" &&
        this.product !== "" &&
        this.template !== ""
      );
    }
  },
  methods: {
    reloadUI: function() {
      window._backend.uiReloadModal();
    },

    closeUI: function() {
      window._frontend.close();
    },

    /**
     * Handle merge
     **/

    initializeMerge: function() {
      this.isMergeInProgress = true;

      var _this = this;
      window._backend
        .withFailureHandler(_this.onMergeFailure)
        .withSuccessHandler(_this.onMergeSuccess)
        .uiStartMerge(
          _this.spreadsheet.id,
          _this.sheet.title,
          _this.product,
          _this.template
        );

      // this.$store.dispatch("saveToLocalStorage");
    },
    onMergeSuccess(data?: {
      metadata: { metrics: { labels: number }; document: { url: string } };
    }): void {
      if (!data || !data.metadata) {
        window._logger(new Error("Merge did not returned metadata"));
        return;
      }

      this.isMergeInProgress = false;
      this.$store.dispatch("setDocument", data.metadata.document.url);
      this.$store.dispatch("setLocation", "/ready");

      window.gtag("event", "merge", {
        event_category: "engagement",
        event_label: this.product,
        value: data.metadata.metrics.labels || 0
      });

      window.saasmetrics("event", {
        category: "engagement",
        action: "merge",
        label: this.product,
        value: data.metadata.metrics.labels || 0
      });
    },
    onMergeFailure(data: any): void {
      this.isMergeInProgress = false;
      window._logger(data);

      window.saasmetrics("event", {
        category: "engagement",
        action: "merge_failure",
        label: this.product
      });
    }
  }
});
