


















































































import Vue from "vue";
import SpreadsheetFilename from "./LabelsCreateSpreadsheetButton.vue";

declare global {
  interface Window {
    gtag: any;
    _backend: any;
  }
}

export default Vue.extend({
  name: "LabelsCreateSpreadsheet",
  components: { SpreadsheetFilename },
  data: function() {
    return {
      sheets: [] as { title: string }[]
    };
  },
  mounted(): void {
    this.initializeSheets();
  },
  computed: {
    isSpreadsheetPicked: function(): boolean {
      return this.spreadsheet && this.spreadsheet.id !== "";
    },
    spreadsheet() {
      return this.$store.state.spreadsheet;
    },
    sheet: {
      get() {
        return this.$store.state.sheet.title;
      },
      set(value) {
        this.$store.dispatch("setSheet", value);
      }
    }
  },
  methods: {
    /**
     * Handle sheets
     **/

    initializeSheets: function() {
      if (!this.spreadsheet.id) {
        return;
      }

      // Get new sheets
      window._backend
        .withSuccessHandler(this.onSheetsFetched)
        .uiGetSheets(this.spreadsheet.id);
    },

    onSheetsFetched: function(data: { title: string }[]) {
      this.sheets = data;

      // Select the first of the list
      if (data.length > 0 && !this.sheet) {
        this.$store.dispatch("setSheet", data[0].title);
      }
    }
  }
});
