





import Vue from "vue";
import { mapState } from "vuex";
import LabelsCreate from "./LabelsCreate.vue";
import GoogFilePicker from "./GoogFilePicker.vue";
import LabelsFinder from "./LabelsFinder.vue";
import LabelsReady from "./LabelsReady.vue";
import LabelsTester from "./TeamOnly/LabelsTester.vue";

const routes = {
  "/": LabelsCreate,
  "/filepicker": GoogFilePicker,
  "/labels": LabelsFinder,
  "/ready": LabelsReady,
  "/team-only/labels-tester": LabelsTester
};

const Router = Vue.extend({
  mounted: function() {
    window.gtag("event", "user_event", { user_account: "free" });
  },
  computed: {
    ...mapState(["location"]),
    routedComponent() {
      window.gtag("config", "UA-136395985-5", { page_path: this.location });
      // @ts-ignore
      return routes[this.location];
    }
  }
});

export default Router;
