import Vuex from "vuex";
import Vue from "vue";

Vue.use(Vuex);

function initializeVuexStateWithContext() {
  const context = window._app && window._app.context;
  const spreadsheet = (context && context.spreadsheet) || {
    id: "",
    url: "",
    title: ""
  };

  const sheet = (context && context.sheet) || {
    title: ""
  };

  return {
    spreadsheet,
    sheet,
    product: "",
    template: "",
    document: "",
    location: (window._app && window._app.location) || "/"
  };
}

export default new Vuex.Store({
  state: initializeVuexStateWithContext(),

  mutations: {
    setSpreadsheet(state, newValue) {
      if (!newValue || !newValue.id) {
        return;
      }

      state.spreadsheet.id = newValue.id;
      state.spreadsheet.url = newValue.url;
      state.spreadsheet.title = newValue.title;
    },

    clearSheet(state) {
      state.sheet.title = "";
    },
    setSheet(state, newValue: string) {
      state.sheet.title = newValue;
    },

    setProduct(state, newValue: string) {
      state.product = newValue;
    },

    setTemplate(state, newValue: string) {
      state.template = newValue;
    },

    setDocument(state, newValue) {
      state.document = newValue;
    },

    setLocation(state, newValue: string) {
      state.location = newValue;
    }
  },

  actions: {
    initializeFromLocalStorage(context) {
      if (!localStorage) {
        return;
      }

      // Retrieve localStorage data
      const localStore = localStorage.getItem("LABELMAKER_V1");
      if (localStore) {
        try {
          const localStoreAsJSON = JSON.parse(localStore);

          const localStoreSavedAt = localStoreAsJSON._at;
          const now = new Date().getTime();
          const expiry = 60 * 60 * 1000;

          // Only assign localStorage data within a specified time window
          if (now - localStoreSavedAt > expiry) {
            return;
          }

          context.commit("setProduct", localStoreAsJSON.product || "");
          context.commit("setSpreadsheet", localStoreAsJSON.spreadsheet);
          context.commit("setSheet", localStoreAsJSON.sheet || "");
          context.commit("setTemplate", localStoreAsJSON.template || "");
        } catch (err) {
          window._logger(err);
        }
      }
    },

    saveToLocalStorage(context) {
      if (!localStorage) {
        return;
      }

      // Save data to localStorage
      localStorage.setItem(
        "LABELMAKER_V1",
        JSON.stringify({
          _at: new Date().getTime(),
          spreadsheet: context.state.spreadsheet,
          sheet: context.state.sheet,
          product: context.state.product,
          template: context.state.template
        })
      );
    },

    setSpreadsheet: function(context, newValue) {
      context.commit("setSpreadsheet", newValue);
    },

    clearSheet: function(context) {
      context.commit("clearSheet");
    },
    setSheet: function(context, newValue: string) {
      context.commit("setSheet", newValue);
    },

    setProduct(context, newValue) {
      context.commit("setProduct", newValue);
    },

    setTemplate(context, newValue) {
      context.commit("setTemplate", newValue);
    },

    setDocument(context, newValue) {
      context.commit("setDocument", newValue);
    },

    setLocation(context, newValue) {
      context.commit("setLocation", newValue);
    }
  }
});
