




















import Vue from "vue";

export default Vue.extend({
  name: "CreateLabelsDesign",
  computed: {
    template: {
      get() {
        return this.$store.state.template;
      },
      set(newValue) {
        this.$store.dispatch("setTemplate", newValue);
      }
    },
    mergefieldsPlaceholder(): string {
      return [
        "<<firstname>> <<lastname>>",
        "<<address>>",
        "<<zipcode>> <<city>>"
      ].join("\n");
    }
  }
});
