import Vue from "vue";
import App from "./App.vue";
import store from "./Store";

Vue.config.productionTip = true;

declare global {
  interface Window {
    saasmetrics(arg0: string, arg1: any, arg2?: any): void;
    gtag: any;
    _backend: any;
    _frontend: any;
    _logger: any;
    _catalog: {
      allLabels: string[][];
      allManufacturers: { [key: string]: string[] };
      allLabelsByManufacturers: { [key: string]: string[] };
    };
    _app?: {
      app_version: string;
      mode: "sheets" | "docs";
      location?: string;
      context?: {
        spreadsheet: {
          id: string;
          url: string;
          title: string;
        };
        sheet: { title: string };
      };
    };
  }
}

new Vue({
  store,
  beforeCreate() {
    // this.$store.dispatch("initializeFromLocalStorage");
  },
  render: h => h(App)
}).$mount("#app");
