


















































import Vue from "vue";

export default Vue.extend({
  name: "LabelsFinder",
  data: function() {
    return {
      manufacturer: "Avery US Letter" as string,
      manufacturers: {} as { [key: string]: string[] },
      product: "" as string,
      products: [] as string[],
      separator: " - " as string
    };
  },
  beforeCreate() {
    // Fetch labels catalog if not fetched already
    if (!window._catalog) {
      const appVersion = (window._app && window._app.app_version) || "2.11";
      const d = document;
      const catalog = d.createElement("script");
      catalog.src =
        "https://cdn.jsdelivr.net/gh/frenchcooc/labelmaker-landing@" +
        appVersion +
        "/dist/js/catalog.min.js";
      catalog.async = true;
      catalog.defer = true;
      d.body.appendChild(catalog);
    }
  },
  mounted: function() {
    this.fetchCatalog();
    this.setProductFromStore();
  },
  methods: {
    labelCreationUI: function() {
      const product = [this.manufacturer, this.separator, this.product].join(
        ""
      );

      this.$store.dispatch("setProduct", product);
      this.$store.dispatch("setLocation", "/");

      window.gtag("event", "finder", {
        event_category: "engagement",
        event_label: product
      });

      window.saasmetrics("event", {
        category: "engagement",
        action: "finder",
        label: product
      });
    },

    closeUI: function() {
      window._frontend.close();
    },

    fetchCatalog: function() {
      if (!window._catalog) {
        return window.setTimeout(this.fetchCatalog, 200);
      }

      this.manufacturers = window._catalog.allManufacturers;
      this.setProducts();
    },

    setProductFromStore() {
      const stateProduct = this.$store.state.product as string;
      if (stateProduct && typeof stateProduct === "string") {
        const productParts = stateProduct.split(this.separator);
        this.manufacturer = productParts.shift() as string;
        this.setProducts();
        this.product = productParts.join(this.separator);
      }
    },

    setProducts() {
      const products =
        window._catalog.allLabelsByManufacturers[this.manufacturer];
      this.products = products.sort();
    }
  }
});
