






















































import Vue from "vue";

export default Vue.extend({
  name: "LabelsReady",
  data() {
    return {
      isMergeInProgress: false
    };
  },
  computed: {
    mode: function() {
      return (window._app && window._app.mode) || "docs";
    },
    documentUrl: function() {
      return this.$store.state.document;
    }
  },
  methods: {
    createUI: function() {
      this.$store.dispatch("setLocation", "/");
    },

    closeUI: function() {
      window._frontend.close();
    }
  }
});
